  .InicioDisplay .grid figure {
    position: relative;
    z-index: 1;
    display: inline-block;
    overflow: hidden;
    margin: 0 0 -0.5em 0;
    width: 25%;
    border: 1.3px solid;
    height: 400px;
    background: #3085a3;
    text-align: center;
    cursor: pointer; }
  .InicioDisplay .grid figure img {
    position: relative;
    display: block;
    min-height: 100%;
    opacity: 0.8; }
  .InicioDisplay .grid figure figcaption {
    padding: 2em;
    color: #fff;
    font-size: 1.25em;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden; }
  .InicioDisplay .grid figure figcaption::before,
  .InicioDisplay .grid figure figcaption::after {
    pointer-events: none; }
  .InicioDisplay .grid figure figcaption,
  .InicioDisplay .grid figure a {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .InicioDisplay .grid figure a {
    z-index: 1000;
    text-indent: 200%;
    white-space: nowrap;
    font-size: 0;
    opacity: 0; }
  .InicioDisplay .grid figure h2 {
    word-spacing: -0.15em;
    font-weight: 300; }
  .InicioDisplay .grid figure h2 span {
    font-weight: 800;
    text-transform: uppercase; }
  .InicioDisplay .grid figure h2,
  .InicioDisplay .grid figure p {
    margin: 0; }
  .InicioDisplay .grid figure p {
    letter-spacing: 1px;
    font-size: 15px; }
  .InicioDisplay figure.effect-ruby {
    background-color: #17819c; }
  .InicioDisplay figure.effect-ruby img {
    opacity: 0.7;
    width: 100%;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
    -webkit-transform: scale(1.15);
    transform: scale(1.15); }
  .InicioDisplay figure.effect-ruby:hover img {
    opacity: 0.5;
    -webkit-transform: scale(1);
    transform: scale(1); }
  .InicioDisplay figure.effect-ruby h2 {
    margin-top: 20%;
    color: white;
    transition: -webkit-transform 0.35s;
    transition: transform 0.35s;
    transition: transform 0.35s, -webkit-transform 0.35s;
    -webkit-transform: translate3d(0, 20px, 0);
    transform: translate3d(0, 20px, 0); }
  .InicioDisplay figure.effect-ruby p {
    margin: 1em 0 0;
    padding: 2.5em;
    border: 1px solid #fff;
    opacity: 0;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
    -webkit-transform: translate3d(0, 20px, 0) scale(1.1);
    transform: translate3d(0, 20px, 0) scale(1.1); }
  .InicioDisplay figure.effect-ruby:hover h2 {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); }
  .InicioDisplay figure.effect-ruby:hover p {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0) scale(1);
    transform: translate3d(0, 0, 0) scale(1); }
